export default {
  data() {
    return {
	  hidden:false,
      current: '',//''
      choose: 'buy',//'' buy shoppingCart
      chooseIndex: 1,//''
      chooseSku:{"skuStock":4, "skuName":"黑色","skuQty":1,"marketPrice":"99","retailPrice":"88","mainImg":"https://p5.ssl.qhimgs1.com/bdr/326__/t01e7473d55942066af.png"},//''
      list:["https://p5.ssl.qhimgs1.com/bdr/326__/t01e7473d55942066af.png","https://p5.ssl.qhimgs1.com/bdr/326__/t01e7473d55942066af.png","https://p5.ssl.qhimgs1.com/bdr/326__/t01e7473d55942066af.png"],//[]
      detailImgList:["https://p5.ssl.qhimgs1.com/bdr/326__/t01e7473d55942066af.png","https://p5.ssl.qhimgs1.com/bdr/326__/t01e7473d55942066af.png","https://p5.ssl.qhimgs1.com/bdr/326__/t01e7473d55942066af.png"],//''
      all: {"goodsName":"小红鞋", "ensureList":[{"rank":2,"content":"contettext"},{"rank":"22","content":"contettext"}]},//''
      goodsSkuVoList: [{"skuStock":2, "skuName":"黑色","skuQty":1,"marketPrice":"99","retailPrice":"88","mainImg":"https://p5.ssl.qhimgs1.com/bdr/326__/t01e7473d55942066af.png"}],//[]
      shoppingCartNum: '',//''
      totalPrice: ''//''
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    getApp().globalData.login();
    uni.request({
      url: getApp().globalData.url2 + getApp().globalData.getGoodsDetailsById + options.goodsId,
      header: {
        type: 1,
        aid: 106
      },
      method: 'get',
      success:(res)=>{
        if (res.data.data.errorCode == 200) {
          for (var i = 0; i < res.data.data.goodsVo.carouselImgList.length; i++) {
            var img = getApp().globalData.imgUrl + "/" + res.data.data.goodsVo.carouselImgList[i];
            res.data.data.goodsVo.carouselImgList[i] = img;
            this.list.push(res.data.data.goodsVo.carouselImgList[i]);
          }
          for (var i = 0; i < res.data.data.goodsVo.goodsSkuVoList.length; i++) {
            var img = getApp().globalData.imgUrl + "/" + res.data.data.goodsVo.goodsSkuVoList[i].mainImg;
            var skuQty = 1;
            res.data.data.goodsVo.goodsSkuVoList[i].skuQty = skuQty;
            res.data.data.goodsVo.goodsSkuVoList[i].mainImg = img;
          }

          for (var i = 0; i < res.data.data.goodsVo.detailImgList.length; i++) {
            var img = getApp().globalData.imgUrl + "/" + res.data.data.goodsVo.detailImgList[i];
            res.data.data.goodsVo.detailImgList[i] = img;
          }

          for (var keyId in res.data.data.goodsVo.goodsSkuVoList) {
            this.goodsSkuVoList.push(res.data.data.goodsVo.goodsSkuVoList[keyId]);
          }
            this.all=res.data.data.goodsVo;
            this.detailImgList=res.data.data.goodsVo.detailImgList;
            this.chooseSku=this.goodsSkuVoList[0];
            this.hidden=true;
        }
      }
    });
  },

  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {
    this.pageLoading = false;
    if (getApp().globalData.memId) {
      uni.request({
        url: getApp().globalData.url1 + getApp().globalData.getShoppingCartCount + getApp().globalData.memId,
        data: '',
        header: {
          aid: 107
        },
        method: 'GET',
        dataType: 'json',
        responseType: 'text',
        success:(res)=>{
          if (res.data.data.errorCode == 200) {
              this.shoppingCartNum=res.data.data.shoppingCartNum;
              this.hidden=true;
          }
        },
        fail: function (res) {},
        complete: function (res) {}
      });
    }
  },

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {},

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function () {},

  /**
   * 用户点击右上角分享
   */
  onShareAppMessage: function () {},
  methods: {
    swiperChange: function (e) {
        this.current=e.detail.current;
    },
    preventD: function () {},

    previewImage1(idx) {
      uni.previewImage({
        current: this.list[idx],
        urls: this.list
      });
    },
    previewImage2(e) {
      var src = e.currentTarget.dataset.src;
      var srcArray = [];
      srcArray.push(src);
      uni.previewImage({
        current: src,
        urls: srcArray
      });
    },
    shoppingCart() {
		uni.navigateTo({
		  url: '/pages/shoppingCart/shoppingCart'
		});
      if (getApp().globalData.memId) {
        if (!this.pageLoading) {
          this.pageLoading = true;
          uni.navigateTo({
            url: '/pages/shoppingCart/shoppingCart'
          });
        }
      } else {
        uni.showModal({
          title: '提示',
          content: '小可爱，微信授权一键登录',
          success: (res)=>{
            if (res.confirm) {
              uni.navigateTo({
                url: '/pages/authorization/authorization'
              });
            } else if (res.cancel) {}
          }
        });
      }
    },

    orderDetails(e) {
      var Settlement = [{}];
      Settlement[0].goodsId = this.all.id;
      Settlement[0].skuId = this.chooseSku.id;
      Settlement[0].skuQty = this.chooseSku.skuQty;
	  uni.navigateTo({
	    url: '/pages/orderDetails/orderDetails?parendNo=' + 2
	  });
      if (this.hidden) {
          this.hidden=false;
        uni.request({
          url: getApp().globalData.url3 + getApp().globalData.generatingOrder,
          data: {
            wxMemId: getApp().globalData.memId,
            openId: getApp().globalData.openId,
            orderSource: getApp().globalData.userType,
            orderVoList: Settlement
          },
          header: {
            aid: 119
          },
          method: 'post',
          dataType: 'json',
          responseType: 'text',
          success: (res)=>{
            if (!this.pageLoading) {
              this.pageLoading = true;
              uni.navigateTo({
                url: '/pages/orderDetails/orderDetails?parendNo=' + res.data.data.parendNo
              });
               this.choose='';
            }
          },
          fail: function (res) {},
          complete: function (res) {}
        });
      }
    },

    shoppingCartChoose() {
      if (getApp().globalData.memId) {
          this.choose='shoppingCart';
      } else {
        uni.showModal({
          title: '提示',
          content: '小可爱，微信授权一键登录',
          success:(res)=>{
            if (res.confirm) {
              uni.navigateTo({
                url: '/pages/authorization/authorization'
              });
            } else if (res.cancel) {}
          }
        });
      }
    },

    buyChoose() {
      if (getApp().globalData.memId) {
          this.choose="buy";
      } else {
        uni.showModal({
          title: '提示',
          content: '小可爱，微信授权一键登录',
          success:(res)=>{
            if (res.confirm) {
              uni.navigateTo({
                url: '/pages/authorization/authorization'
              });
            } else if (res.cancel) {}
          }
        });
      }
    },

    joinShoppingCart() {
      uni.request({
        url: getApp().globalData.url1 + getApp().globalData.addAShoppingCart,
        data: {
          memId: getApp().globalData.memId,
          memSource: 1,
          skuId: this.chooseSku.id,
          qty: this.chooseSku.skuQty
        },
        header: {
          aid: 114
        },
        method: 'post',
        dataType: 'json',
        responseType: 'text',
        success: function (res) {
          uni.request({
            url: getApp().globalData.url1 + getApp().globalData.getShoppingCartCount + getApp().globalData.memId,
            data: '',
            header: {
              aid: 107
            },
            method: 'GET',
            dataType: 'json',
            responseType: 'text',
            success:(res)=>{
                this.shoppingCartNum=res.data.data.shoppingCartNum;
            },
            fail: function (res) {},
            complete: function (res) {}
          });
        },
        fail: function (res) {},
        complete: function (res) {}
      });
       this.choose='';
    },

    close() {
        this.choose='';
    },

    chooseType(idx) {
        this.chooseIndex=idx;
        this.chooseSku=this.goodsSkuVoList[idx];
    },

    //减少数量
    reduce: function () {
      var skuVoList = {};
      if (this.chooseSku.skuQty >= 2) {
        skuVoList.skuQty = this.chooseSku.skuQty - 1;
         this.chooseSku.skuQty=skuVoList.skuQty;
      }
    },
    //增加数量
    add: function () {
      var skuVoList = {};
      if (this.chooseSku.skuQty < this.chooseSku.skuStock) {
        skuVoList.skuQty = this.chooseSku.skuQty + 1;
          this.chooseSku.skuQty=skuVoList.skuQty;
      }
    }
  }
};
